;(function () {

	'use strict';

	var app = App.prototype;

	function App() {
		this.init();
	};

	app.init = function () {
		app.onAutomaticRevealTopMenu();
	};

	app.onAutomaticRevealTopMenu = function () {
		$(window).scroll(function() {

			var mobileNav 	 = $("#fixContactMob"),
				desktopNav   = $("#top-bar"),
				footerHeight = $('#footer').height()+80,
				bodyHeight   = $('body').height();

			if ((bodyHeight-footerHeight) <= ($(window).height() + $(window).scrollTop())) {
				mobileNav.css("opacity", 0 );
				desktopNav.css("opacity", 0 );
			} else {
				mobileNav.css("opacity", 1 );
				desktopNav.css("opacity", 1 );
			}
		});
	};

	document.addEventListener( 'DOMContentLoaded', new App(), false );

})();



