<template>
	<div class="ui-slider ui-slider-horizontal ui-widget ui-widget-content ui-corner-all" aria-disabled="false">
		<input type="text" class="first-slider-value"  :value="getFormattedMinValue" readonly="readonly">
		<input type="text" class="second-slider-value" :value="getFormattedMaxValue" readonly="readonly">
	</div>
</template>

<script>

	import 'jquery-ui/ui/widgets/slider'

	export default {

		name: 'range-slider',

		props: {
			options: Object
		},

		data () {
			return  {
				min: 0,
				max: this.options.max
			}
		},

		computed: {

			getFormattedMinValue () {
				return `${this.min} ${this.options.unit}`
			},

			getFormattedMaxValue () {
				return `${this.max} ${this.options.unit}`
			}
		},

		mounted () {

			var vm = this

			//init
			$(this.$el)

			.slider({
				
				range: true,
				min: 0,
				max: this.options.max,
				// step: (this.options.max/(this.options.max/2)),
				step: 500,
				values: [0, this.options.max],

				slide: function(e, ui) {
					vm.min = ui.values[0]
					vm.max = ui.values[1]
				},

				change: function(e, ui) {
					let formatted =  { id: vm.options.id, max: vm.max, min: vm.min, del: (vm.min == 0 && vm.max == vm.options.max) }
					vm.$emit('rangeSelected', formatted)
				}
			})
		},

		destroyed () {
			$(this.$el).off().slider('destroy')
		}
	}	

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

	.range-slider {
		margin: 1em 0;
	}

</style>



