// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from '@/components/App'
import Navigation from '@/components/Navigation'
import FooterDetail from '@/components/Footer'
import router from '@/router'
// import store from '@/store'
// import {atLeastFixNumber, slugUrl} from '@/helpers'
import '@/bootstrap'

/* layout components */
Vue.component('app', App);
Vue.component('navigation', Navigation);
Vue.component('footer-detail', FooterDetail);

/* directives */
// import '@/directives/RangeSlider'

/* compiled vendor libraries */
import 'bootstrap/dist/js/bootstrap.min.js';
import 'jquery-ui-touch-punch';
import 'font-awesome/css/font-awesome.min.css'

/* jquery actionables */
import '@/utils/actionable.js';
import '@/utils/custom.js';

/* configurations */
Vue.config.productionTip = false

const app = new Vue({
	router: router
	// store: store
}).$mount('#app')




